<template>
  <div class="cms-list-layout" style="padding-top:6px">
    <section class="cms-list-query">
      <div class="query-group">
        <label>结算周期</label>
        <el-select v-model="queryParams.cycleId" placeholder="请选择" @change="query">
          <el-option v-for="(item) in cycleList" :key="item.id"
           :label="$moment(item.begintime,'YYYY/MM/DD')+'~'+$moment(item.endtime,'YYYY/MM/DD')" :value="item.id"></el-option>
        </el-select>
      </div>
      <!-- <div class="query-group">
        <label>商家</label>
        <el-input v-model="store"></el-input>
      </div> -->
      <div class="query-group">
        <label>状态</label>
        <el-select v-model="statuses" placeholder="请选择" @change="statusQuery">
          <el-option label="全部" value=""></el-option>
          <el-option label="待商家确认" :value="1"></el-option>
          <el-option label="待平台审核" :value="2"></el-option>
          <!-- <el-option label="已结算" :value="3"></el-option> -->
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="exportData"><i class="iconfont icon-Excel"></i> 导出</cms-button>
      </div>
    </section>
    <section class="cms-batch">
      <cms-button icon="success" type="success" @click="allExamine(ids)" size="small">确认审核</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
        <el-table-column type="selection" width="46" fixed='left'></el-table-column>
        <el-table-column type="expand" align="right">
          <template slot-scope="props">
            <el-form label-position="right" label-width="160px" inline class="demo-table-expand">
              <el-form-item label="结算批次号：">
                <span>{{ props.row.businessNumber }}</span>
              </el-form-item>
              <el-form-item label="商家：">
                <span>{{ props.row.store.storeName }}</span>
              </el-form-item>
              <el-form-item label="主营类目：">
                <span>{{ props.row.store.productCategory.cateName }}</span>
              </el-form-item>
              <el-form-item label="生成时间：">
                <span>{{ $moment(props.row.createTime,'YYYY-MM-DD HH:mm:ss') }}</span>
              </el-form-item>
              <el-form-item label="状态：">
                <span v-if="props.row.status == 1">待商家确认</span>
                <span v-if="props.row.status == 2">待平台审核</span>
                <span v-if="props.row.status == 3">已结算</span>
              </el-form-item>
              <el-form-item label="有效订单数：">
                <span class="green">{{ props.row.orders>0?props.row.orders:0 }} 笔</span>
              </el-form-item>
              <el-form-item label="退款单数：">
                <span class="red">{{ props.row.refundOrders>0?props.row.refundOrders:0 }} 笔</span>
              </el-form-item>
              <el-form-item label="结算金额：">
                <span class="green">￥ {{ $cms.float(props.row.amount) }}</span>
              </el-form-item>
              <el-form-item label="">
                <span></span>
              </el-form-item>
              <el-form-item label="成交金额：">
                <span class="green">￥ {{ $cms.float(props.row.turnoverAmount) }}</span>
              </el-form-item>
              <el-form-item label="">
                <span></span>
              </el-form-item>
              <el-form-item label="">
                <span></span>
              </el-form-item>
              <el-form-item label="积分抵扣（金额/数量）：">
                <span class="green"> ￥{{$cms.float(props.row.integralDeductionAmount)}}/{{props.row.integralDeductionNum}}</span>
              </el-form-item>
              <el-form-item label="红包抵扣（金额/数量）：">
                <span class="green"> ￥{{$cms.float(props.row.redenvelopeOffset)}}/{{props.row.redenvelopeOffsetNum}}</span>
              </el-form-item>
              <el-form-item label="平台优惠券抵扣：">
                <span class="green"> ￥ {{$cms.float(props.row.platformCouponOffset)}}</span>
              </el-form-item>
              <el-form-item label="退款金额：">
                <span class="red">- ￥ {{ $cms.float(props.row.refundAmount) }}</span>
              </el-form-item>
              <el-form-item label="">
                <span></span>
              </el-form-item>
              <el-form-item label="">
                <span></span>
              </el-form-item>
              <el-form-item label="积分返还（金额/数量）：">
                <span class="red">- ￥{{$cms.float(props.row.refundRedenvelopeAmount)}}/{{props.row.refundRedenvelopeNum}}</span>
              </el-form-item>
              <el-form-item label="红包返还（金额/数量）：">
                <span class="red">- ￥{{$cms.float(props.row.redenvelopeOffset)}}/{{props.row.redenvelopeOffsetNum}}</span>
              </el-form-item>
              <el-form-item label="平台优惠券返还：">
                <span class="red">- ￥ {{$cms.float(props.row.platformRefundCoupon)}}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="businessNumber" label="结算批次号" width="160"></el-table-column>
        <el-table-column prop="settlementCycle" label="结算周期" min-width="180" align="center">
          <div slot-scope="scope">
            <span v-if="scope.row.settlementCycle.begintime&&scope.row.settlementCycle.begintime!=''">
              {{$moment(scope.row.settlementCycle.begintime,'YYYY/MM/DD')+' ~ '+$moment(scope.row.settlementCycle.endtime,'YYYY/MM/DD')}}
            </span>
            <span v-else>{{$moment(scope.row.settlementCycle.endtime,'YYYY-MM-DD')}}</span>
          </div>
        </el-table-column>
        <el-table-column prop="store.storeName" label="商家" min-width="170" align="center"> </el-table-column>
        <el-table-column prop="store.productCategory.cateName" label="主营类目" min-width="100" align="center"> </el-table-column>
        <el-table-column prop="amount" label="结算金额" min-width="130" align="center">
           <div slot-scope="scope">
            <span>￥ {{ $cms.float(scope.row.amount) }}</span>
          </div>
        </el-table-column>
        <el-table-column prop="createTime" label="生成时间" min-width="150" align="center">
          <div slot-scope="scope">
            <span>{{$moment(scope.row.createTime,'YYYY-MM-DD HH:mm:ss')}}</span>
          </div>
        </el-table-column>
        <el-table-column prop="status" :formatter="toStatus" label="状态" min-width="110" align="center"> </el-table-column>
        <el-table-column prop="hasError" label="是否异常" min-width="80" align="center">
          <div slot-scope="scope">
            <span :class="scope.row.hasError?'green':'red'">
              {{scope.row.hasError?'是':'否'}}
            </span>
          </div>
        </el-table-column>
        <el-table-column label="操作" width='300' fixed="right" align="center">
          <div slot-scope="scope">
            <cms-button v-if="scope.row.status == 1" type='info' size='small' @click="examine(scope.row.id)" slot="reference">审核</cms-button>
            <cms-button  type='info' size='small' 
            @click.stop="$routerLink('/statisticsSheet/abnormal','list',
            {sid:scope.row.id,id:scope.row.id,isEdit:(scope.row.status == 1?true:false)})" 
           slot="reference">查看异常数据</cms-button>
            <cms-button  type='info' size='small' 
            @click.stop="$routerLink('/statisticsSheet/details','list',
            {sid:scope.row.id,id:scope.row.settlementCycle.id,storeId:scope.row.store.id,businessNumber:scope.row.businessNumber,storeName:scope.row.store.storeName,isEdit:(scope.row.status == 1?true:false)})" 
            slot="reference">查看明细</cms-button>
          </div>
        </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>

  </div>
</template>

<script>
import { storesettlementPage, storesettlementcycleList, storesettlementExamine, storesettlementExport } from '@/api/finance/finance.js'
import list from '@/mixins/list'
var defaultData = {
  page: 1,
  size: 10,
  statuses: [1, 2],
  cycleId: ''
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      loading: false,
      status: '',
      store: '',
      cycleList: [],
      statuses: ''
    }
  },
  methods: {
    statusQuery(val) {
      if (val === '') {
        this.queryParams.statuses = [1, 2]
      } else {
        this.queryParams.statuses = [val]
      }
      this.query()
    },
    exportData() { // 导出
      delete this.queryParams.page
      delete this.queryParams.size
      storesettlementExport(this.queryParams).then(res => {
        const blob = new Blob([res])
        var a = document.createElement('a')
        a.download = '商家待处理.xls'
        a.href = window.URL.createObjectURL(blob)
        a.click()
      })
    },
    toStatus(row, column) { // 过滤当前状态
      const arr = ['', '待商家确认', '待平台审核', '已结算']
      return arr[parseInt(row.status)]
    },
    getCycleList() { // 获取结算周期列表
      storesettlementcycleList().then(res => {
        if (res.code === 200) {
          this.cycleList = res.data
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    changeData() { // 通过ref来触发
      this.getCycleList()
      this.getTableData(storesettlementPage, this.queryParams)
    },
    examine(id) { // 审核
      this.$confirm('审核通过后，结算单将移交到平台审核，是否继续执行?', '系统提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          storesettlementExamine({ ids: [id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('提交成功,请耐心等待平台审核')
              this.getTableData(storesettlementPage, this.queryParams)
            } else {
              this.$errorMessage(res.message)
            }
          })
        }).catch(() => {})
    },
    allExamine(ids) { // 审核
      if (ids.length > 0) {
        this.$confirm('审核通过后，结算单将移交到平台审核，是否继续执行?', '系统提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            storesettlementExamine({ ids: ids }).then(res => {
              if (res.code === 200) {
                this.$successMessage('提交成功,请耐心等待平台审核')
                this.getTableData(storesettlementPage, this.queryParams)
              } else {
                this.$errorMessage(res.message)
              }
            })
          }).catch(() => {})
      } else {
        this.$errorMessage('请至少选择一项进行审核')
      }
    }
  },
  created() {
    this.getCycleList()
    this.getTableData(storesettlementPage, this.queryParams)
  }
}
</script>
<style lang="scss" scoped>
  .demo-table-expand {
    padding-left: 58px;
    font-size: 0;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 26%;
    padding: 0;
  }
  .green{
    color: green;
  }
  .red{
    color: red;
  }
</style>





