<template>
  <div class="cms-list-layout">
    <cms-prompt  collapse='true'>
      <li>系统会在设置的指定日期开始统计生成商家结算账单</li>
      <li>有效订单包含交易成功且已过售后处理时效订单（售后时效详见商城设置）</li>
      <li>系统生成的结算将自动推送至商户，商户确认无疑义后，平台将进行审核确认后，结算金额将自动充值至商户虚拟账户内，后期商户可自行进行提现操作</li>
      <li>商户针对结算单产生疑义，可填写差额明细并确认结算单反馈至平台，平台将进行审核后再次反馈进行确认</li>
    </cms-prompt>
    <el-tabs v-model="activeName">
      <el-tab-pane label="待处理" name="wait">
        <wait ref='wait' @change="eventChange"></wait>
      </el-tab-pane>
      <el-tab-pane label="已结算" name="settled" lazy>
        <settled ref="settled"  @change="eventChange"></settled>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import wait from './waitDisposal'
import settled from './settled'
export default {
  name: 'statisticsList',
  components: {
    wait,
    settled
  },
  data() {
    return {
      activeName: 'wait'
    }
  },
  methods: {
    eventChange(ev) {
      // 出售中列表状态更新
      if (ev === 'settled') {
        if (this.$refs['settled']) {
          this.$refs['settled'].changeData() // 调用列表更新
        }
      }
      if (ev === 'wait') {
        if (this.$refs['wait']) {
          this.$refs['wait'].changeData() // 调用列表更新
        }
      }
    }

  }
}
</script>

<style>
</style>



